import React from 'react';
import Slider from 'react-slick';

// Import slick-carousel CSS
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

// List of random sushi images
const images = [
  'https://patch.com/img/cdn20/shutterstock/24423246/20240122/125815/styles/patch_image/public/shutterstock-1221793915___22125741509.jpg',
  'https://www.allrecipes.com/thmb/XT7-9MROYJZvNyQR4J40HGOVDmQ=/1500x0/filters:no_upscale():max_bytes(150000):strip_icc()/19511smoked-salmon-sushi-rollfabeveryday4x3-159a22b4d3ac49fe9a146db94b53c930.jpg',
  'https://everydayglutenfreegourmet.ca/wp-content/uploads/2023/07/handmade-sushi-rolls.jpg',
];

const settings = {
  dots: true,
  infinite: true,
  speed: 500,
  slidesToShow: 1,
  slidesToScroll: 1
};

const SushiCarousel = () => (
  <div>
    <Slider {...settings}>
      {images.map((src, index) => (
        <div key={index}>
          <img src={src} alt={`Sushi ${index + 1}`} style={{ width: '100%', height: 'auto' }} />
        </div>
      ))}
    </Slider>
  </div>
);

export default SushiCarousel;
